.presentation{
    margin: auto;
    margin-bottom: 0px;
    width: 100%;
    margin-top: 0px;
    .lastReal{
        width: 90%;
        margin: auto;
        max-width: 1200px;
        .contentPart{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-around;
            align-items: center;
            width: 100%;
            background-color: $color-5;
            border: solid 5px black;
            border-radius: 10px;
            animation: lastProd 400ms ease-in forwards;
            opacity: 0;
            .titlePart{
                width: 100%;
                h2{
                    text-align: center;
                    font-family: "montserrat";
                    font-weight: 400;
                    font-size: 35px;
                    border-radius: 10px;
                    padding: 0px;
                    color: black;
                    margin-bottom: 0px;
                    animation: lastProd 400ms ease-in forwards;
                }
            }
            .lastprodMini{
                padding: 0px;
                width: 26%;
                display: flex;
                animation: lastProd 400ms ease-in forwards;
                opacity: 0;
                a{
                    width: 100%;
                    display: flex;
                    img{
                        margin: auto;
                        width: 70%;
                        transition-duration: 300ms;
                    }
                    :hover{
                        cursor: pointer;
                    }

                }
            }
        }
    }
}


@keyframes lastProd {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@media only screen and (max-width: 800px){
    .presentation{
        margin-top: 10px;
        .lastReal{
            margin: auto;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-family: "montserrat";
            .contentPart{
                padding-bottom: 20px;
                width: 98%;
                border: none;
                box-shadow: 1px 1px 3px rgb(141, 141, 141);
                background-color: white;
                .titlePart{
                    margin-top: 0px;
                    width: 100%;
                    h2{
                        text-align: center;
                        font-family: "montserrat";
                        font-weight: 400;
                        font-size: 25px;
                        border-radius: 10px;
                        padding-top: 25px;
                        color: black;
                        margin-top: 0;
                        margin-bottom: 20px;
                        animation: lastProd 800ms ease-in;
                    }
                }
                .lastprodMini{
                    width: 45%;
                    margin: 0px;
                    img{
                        width: 100%;
                        padding: 0px;
                        margin: auto;
                    }
                    :hover{
                        transform: scale(1);
                        cursor: pointer;
                    }
                }
            }
        }

    }
}